<template>
	<Tabs value="system" class="tab">
		<Tab-pane label="系统管理" name="system" class="tab-item">
			<Table highlight-row :columns="header" :data="lists[page-1]" size="large"></Table>
			<div class="page">
				<Page placement='top' @on-change="pageChange" @on-page-size-change="sizeChange"
					  :total="totalPage"
					  show-elevator
					  show-sizer
					  show-total :page-size="pageSize" :page-size-opts="[10,20,30,40]">
				</Page>
			</div>
			<Modal v-model="editModal" width="500" :loading="true">
				<p slot="header" style="color:#323d4d;font-size:14px;text-align:center">
					<span>编辑账号</span>
				</p>
				<div class="center-mod">
					<Form ref="form" v-if="editModal" :model="userItem" :label-width="60" class="from-self">
						<Form-item label="使用人">
							<Input v-model="userItem.userName" placeholder="填写使用人名字"></Input>
						</Form-item>
						<Form-item label="角色">
							<Select v-model="userItem.roleId" @on-change="selectChange" placeholder="请选择角色">
								<Option v-for="item in roleList" :disabled="item.roleId==1000" :value="item.roleId"
										:key="item.roleId">{{item.roleName}}
								</Option>
							</Select>
						</Form-item>
						<Form-item label="描述">
							<Input v-model="userItem.roleComment" disabled placeholder="角色职责描述"></Input>
						</Form-item>
					</Form>
				</div>
				<div slot="footer" style="text-align: center">
					<i-button type="primary" class="sub-btn" size="large" @click="editUser">确定</i-button>
					<i-button type="ghost" class="cancel-btn" size="large" @click="editModal=false">取消</i-button>
				</div>
			</Modal>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" @click="$router.push('adduser')">
			新建账号
		</i-button>
		<i-button type="text" slot="extra" class="extra-btn" @click="$router.push('editrole')">
			配置角色
		</i-button>
	</Tabs>
</template>
<script>
	export default {
		data () {
			return {
				header: [
					{
						type: 'index',
						title: '序号',
						width: 80,
						align: 'center'
					},
					{
						title: '账号',
						key: 'accountName',
						align: 'center'
					},
					{
						title: '使用人',
						key: 'userName',
						align: 'center'
					},
					{
						title: '角色',
						key: 'roleName',
						align: 'center'
					},
					{
						title: '描述',
						key: 'roleComment',
						align: 'center'
					},
					{
						title: '状态',
						key: 'status',
						align: 'center',
						render: (h, params) => {
							if (params.row.status == '0') {
								return h('span', {
									style: {
										color: '#19be6b'
									}
								}, '正常')
							} else if (params.row.status == '1') {
								return h('span', {
									style: {
										color: '#ed3f14'
									}
								}, '已禁用')
							} else {
								return h('span', {}, '未知')
							}
						},
						filters: [
							{
								label: '正常',
								value: 0
							},
							{
								label: '禁用',
								value: 1
							}
						],
						filterMultiple: false,
						filterMethod (value, row) {
							return row.status == value;
						}
					},
					{
						title: '备注',
						key: 'statusRemark',
						align: 'center'
					},
					{
						title: '操作',
						key: 'action',
						width: 150,
						align: 'center',
						render: (h, params) => {
							let _this = this;
							return h('div', [
								h('i-button', {
									props: {
										type: 'info',
										size: 'small',
										disabled: params.row.roleId == 1000 ? true : false
									},
									style: {
										marginRight: '5px'
									},
									on: {
										click: () => {
											var roleList = _this.loadRoleName();//加载角色列表
											_this.userItem = _.omit(params.row, ['_index', 'password', 'apikey']);
											_this.editModal = true;//打开弹窗
										}
									}
								}, '编辑'),
								h('i-button', {
									props: {
										type: params.row.status == '0' ? 'error' : 'success',
										size: 'small',
										disabled: params.row.roleId == 1000 ? true : false
									},
									style: {
										marginRight: '5px'
									},
									on: {
										click: () => {
											if (params.row.status == '0') {//点击禁用
												_this.$Modal.confirm({
													title: '禁用原因',
													render: (h) => {
														return h('Input', {
															props: {
																value: _this.statusRemark,
																autofocus: true,
																placeholder: '请输入禁用原因...'
															},
															style: {
																marginTop: '20px',
															},
															on: {
																input: (val) => {
																	_this.statusRemark = val;
																},
															}
														})
													},
													loading: true,
													onOk: function () {
														let postParams = _.omit(params.row, '_index');//创建一个去掉序号的行对象副本
														postParams.statusRemark = _this.statusRemark;//禁用原因
														postParams.status = '1';//禁用状态
														_this.$http.post('/user/modifyAccount', postParams)
															.then(function (response) {
																_this.$Modal.remove();
																_this.statusRemark = '';
																_this.loadList();
																_this.$Message.success({
																	content:response.data.msg
																})
															})
															.catch(function (error) {
																_this.$Notice.error({
																	title:error
																})
															});
													},
													onCancel: function () {
														_this.statusRemark = '';
													}
												})
											}
											if (params.row.status == '1') {//点击启用
												this.$Modal.confirm({
													title: '启用账号',
													loading: true,
													content: '<p>确定要启用此账号？</p>',
													onOk: function () {
														//点击了确定
														let postParams = _.omit(params.row, '_index');//创建一个去掉序号的行对象副本
														postParams.statusRemark = '';//清空备注
														postParams.status = '0';//更改使用状态
														_this.$http.post('/user/modifyAccount', postParams)
															.then(function (response) {
																_this.loadList();
																_this.$Modal.remove();
																_this.$Message.success({
																	content:response.data.msg
																})
															})
															.catch(function (error) {
																_this.$Notice.error({
																	title:error
																})
															});
													}
												})
											}
										}
									}
								}, params.row.status == '0' ? '禁用' : '启用')
							])
						}
					}
				],
				lists: [],
				data: [],
				userItem: {},
				roleList: [],
				totalPage: 10,
				pageSize: 10,
				page: 1,
				statusRemark: '',
				editModal: false
			}
		},
		created: function () {
			this.loadList();
		},
		methods: {
			pageChange: function (page) {
				let _this = this;
				if (_this.page != page) {
					_this.page = page;
				}
			},
			sizeChange: function (size) {
				let _this = this;
				_this.pageSize = size;
				_this.lists = _.chunk(_this.data, size);
			},
			loadList: function () {
				let _this = this;
				_this.$http.post('/user/userList', {})
					.then(function (response) {
						//处理响应数据
						let data = response.data;//服务器响应数据
						if(data){
							_this.totalPage = _.size(data);//设置页码
							_this.data = data;//原始数据
							_this.lists = _.chunk(data, _this.pageSize);//表格数据
						}
					})
					.catch(function (error) {
						//处理错误
						_this.$Message.error({
							content: error,
							duration: 3
						});
					});
			},
			loadRoleName: function () {
				let _this = this;
				this.$http.post('/auth/allRoleName', {})
					.then(function (response) {
						if (response.data.code == 1) {
							_this.roleList = response.data.data;
						} else {
							_this.$Message.error({
								content: response.data.msg,
								duration: 3
							});
							return false
						}
					})
					.catch(function (error) {
						_this.$Message.error({
							content: error,
							duration: 3
						});
						_this.$Modal.remove();
						return false
					});
			},
			selectChange: function (val) {
				let _this = this;
				_this.userItem.roleComment = _.result(_.find(_this.roleList, {'roleId': val}), 'roleComment');
				_this.userItem.roleName = _.result(_.find(_this.roleList, {'roleId': val}), 'roleName');
			},
			editUser: function () {
				let _this = this;
				this.$http.post('/user/modifyAccount', _this.userItem)
					.then(function (response) {
						if (response.data.code == 1) {
							_this.editModal = false;
							_this.loadList();
							_this.$Message.success(response.data.msg);
						} else {
							_this.editModal = false;
							_this.$Message.error(response.data.msg);
						}
					})
					.catch(function (error) {
						_this.$Message.error({
							content: error
						});
					});
			}
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.page {
		padding-top: 50px;
		display: flex;
		justify-content: flex-end;
		flex-direction: row;
		padding-bottom: 50px;
	}

	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}

	.modal-input {
		width: 100%;
		height: 30px;
		line-height: 30px;
		padding: 0 10px;
		border-radius: 4px;
		border: 1px solid #dddee1;
		background-color: #fff;
	}

	.sub-btn {
		width: 120px;
		margin-right: 10px;
	}

	.cancel-btn {
		width: 120px;
		color: #2d8cf0;
		border: 1px solid #2d8cf0;
	}
</style>



